import React from "react";
import { ReactComponent as BuildingBkgImg } from "./assets/art/svg/buildings_bkg.svg";
import { makeStyles } from "@material-ui/core/styles";
export default function BuildingBkg(props) {
  const useStyles = makeStyles((theme) => ({
    building: {
      position: "absolute",
      left: "50%",
      transform: "translateX(-50%)",
      marigin: "0 auto",
      height: (300 * props.height) / 1080,
      width: (1980 * props.height) / 1080
    }
  }));
  const classes = useStyles();
  return (
    <div className={classes.building}>
      <BuildingBkgImg />
    </div>
  );
}
